import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { map } from 'rxjs/operators';
import { environment } from '../../../environments/environment';

@Injectable()
export class MembershipService {
    getAPI: any;
    public oi = localStorage.getItem('oi');
    constructor(private http: HttpClient) {
     this.getAPI = environment.api;  }

    getMembership() {
        return new Promise(resolve => {
        this.http.get(this.getAPI.GETMEMBERSHIPAPI+'?action=get&oi='+this.oi+'&q='+new Date().getTime())
            .subscribe(users => {
                console.log(users);
                resolve(users);
                }, err => {
               console.log("vbn"+err);
            });
        });
    }
    getMembershipTime(time){
        return new Promise(resolve => {
        this.http.get(this.getAPI.GETMEMBERSHIPAPI+'?action=get&oi='+this.oi+'&q='+time)
            .subscribe(users => {
                console.log(users);
                resolve(users);
                }, err => {
               console.log("vbn"+err);
            });
        });
    }
    getMembershipById(id) {
        return new Promise(resolve => {
        this.http.post(this.getAPI.GETMEMBERSHIPAPI+'?action=id&oi='+this.oi+'&q='+new Date().getTime(), {id})
            .subscribe(users => {
                console.log(users);
                resolve(users);
                }, err => {
               console.log("vbn"+err);
            });
        });
    }
    addMembership(memDetail) {
        return new Promise(resolve => {
        this.http.post(this.getAPI.ADDMEMBERSHIPAPI+'?action=add&oi='+this.oi+'&q='+new Date().getTime(), { memDetail: memDetail })
            .subscribe(users => {
                console.log(users);
                resolve(users);
                }, err => {
               console.log("vbn"+err);
            });
        });
    }
    updateMembership(memDetail) {
        return new Promise(resolve => {
        this.http.post(this.getAPI.ADDMEMBERSHIPAPI+'?action=update&oi='+this.oi+'&q='+new Date().getTime(), { memDetail })
            .subscribe(users => {
                console.log(users);
                resolve(users);
                }, err => {
               console.log("vbn"+err);
            });
        });
    }
    deleteMembership(id) {
        return new Promise(resolve => {
        this.http.post(this.getAPI.ADDMEMBERSHIPAPI+'?action=delete&oi='+this.oi+'&q='+new Date().getTime(), { id })
            .subscribe(users => {
                console.log(users);
                resolve(users);
                }, err => {
               console.log("vbn"+err);
            });
        });
    }
}
