    addMenu(mainMenu, userMenu, action){
      if(action == 'saveModule'){
        return new Promise(resolve => {
        this.http.post(this.getAPI.MODULESETTINGSAPI+'?action=addmenu&oi='+this.oi+'&q='+new Date().getTime(), { mainMenu, userMenu })
            .subscribe(settingValue => {
                console.log(settingValue);
                resolve(settingValue);
                }, err => {
               console.log("vbn"+ JSON.stringify(err));
            });
        });
      }
      else{
        return new Promise(resolve => {
        this.http.post(this.getAPI.MODULESETTINGSAPI+'?action=updatemenu&oi='+this.oi+'&q='+new Date().getTime(), { mainMenu, userMenu })
            .subscribe(settingValue => {
                console.log(settingValue);
                resolve(settingValue);
                }, err => {
               console.log("vbn"+ JSON.stringify(err));
            });
        });
      }
    }
    addMenuOrgId(mainMenu, userMenu, action, orgId){
      return new Promise(resolve => {
      this.http.post(this.getAPI.MODULESETTINGSAPI+'?action=addmenu&oi='+encodeURIComponent(window.btoa(orgId+'_u$0r'))+'&q='+new Date().getTime(), { mainMenu, userMenu })
          .subscribe(settingValue => {
              console.log(settingValue);
              resolve(settingValue);
              }, err => {
             console.log("vbn"+ JSON.stringify(err));
          });
      });
    }
