import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { map } from 'rxjs/operators';
import { environment } from '../../../environments/environment';

@Injectable()
export class MembershipSegmentsService {
    getAPI: any;
    constructor(private http: HttpClient) {
     this.getAPI = environment.api;  }

    addSegment(mydata){
      return new Promise(resolve => {
        this.http.post(this.getAPI.ADDSEGMENTSAPI+'?action=add&oi='+localStorage.getItem('oi')+'&q='+Date.now(), { data: mydata })
        .subscribe(data => {
          console.log(data);
          resolve(data);
        }, err => {
          console.log("vbn"+err);
        });
      });
    }

    getSegmentByOrg() {
        return new Promise(resolve => {
        this.http.get(this.getAPI.ADDSEGMENTSAPI+'?action=get&oi='+localStorage.getItem('oi')+'&q='+Date.now())
            .subscribe(data => {
                console.log(data);
                resolve(data);
                }, err => {
               console.log("vbn"+err);
            });
        });
    }


    deleteSegment(id){
        return new Promise(resolve => {
        this.http.post(this.getAPI.ADDSEGMENTSAPI+'?action=delete&oi='+localStorage.getItem('oi')+'&q='+Date.now(), { id })
            .subscribe(data => {
                console.log(data);
                resolve(data);
                }, err => {
               console.log("vbn"+err);
            });
        });
    }
    /* End of Sandeep's Code */
}
