import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { map } from 'rxjs/operators';
import { environment } from '../../../environments/environment';

@Injectable()
export class VerifyNewOrganization {
    getAPI: any;
    constructor(private http: HttpClient) {
    this.getAPI = environment.api }

    saveOrganization(organizationDetail) {
        return new Promise(resolve => {
        this.http.post(this.getAPI.USERORGANIZATIONAPI+'?action=insert&q='+new Date(), { organizationDetail })
            .subscribe(subscriptionValue => {
                console.log(subscriptionValue);
                resolve(subscriptionValue);
                }, err => {
               console.log("vbn"+ JSON.stringify(err));
            });
        });
    }

    updateUserOrganization(orgId, userId) {
        return new Promise(resolve => {
        this.http.post(this.getAPI.USERORGANIZATIONAPI+'?action=update&q='+new Date(), { orgId, userId })
            .subscribe(subscriptionValue => {
                console.log(subscriptionValue);
                resolve(subscriptionValue);
                }, err => {
               console.log("vbn"+ JSON.stringify(err));
            });
        });
    }
}
