import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { map } from 'rxjs/operators';
import { environment } from '../../../environments/environment';

@Injectable()
export class ImportLogService {
  getAPI: any;

    options = {
        types: ['geocode'],
        componentRestrictions: { country: 'AU' }
    }

    constructor(private http: HttpClient) {
             this.getAPI = environment.api;}
           public oi = localStorage.getItem('oi');
    public timestamp = new Date().getTime();

    getImportLog() {
        return new Promise(resolve => {
          //alert(localStorage.getItem('currentUserId'));
        this.http.get(this.getAPI.GETIMPORTLOGAPI+'?user_id='+localStorage.getItem('currentUserId')+'&q='+this.timestamp)
            .subscribe(lists => {
                console.log(lists);
                resolve(lists);
                }, err => {
                  alert(JSON.stringify(err));
               console.log("vbn"+ JSON.stringify(err));
            });
        });
    }
    filter(whereClause){
    return new Promise(resolve => {
      //alert(localStorage.getItem('currentUserId'));
    this.http.get(this.getAPI.GETIMPORTLOGAPI+'?user_id='+localStorage.getItem('currentUserId')+'&q='+this.timestamp +'&whereClause=' + whereClause)
        .subscribe(lists => {
            console.log(lists);
            resolve(lists);
            }, err => {
              alert(JSON.stringify(err));
           console.log("vbn"+ JSON.stringify(err));
        });
    });
}
getImportLogById(id) {
        return new Promise(resolve => {
          //alert(localStorage.getItem('currentUserId'));
        this.http.get(this.getAPI.GETIMPORTLOGAPI+'?id='+ id +'&q='+this.timestamp)
            .subscribe(lists => {
                console.log(lists);
                resolve(lists);
                }, err => {
                  alert(JSON.stringify(err));
               console.log("vbn"+ JSON.stringify(err));
            });
        });
    }

    getLogDetail(task_id){
        return new Promise(resolve => {
        this.http.get(this.getAPI.GETLOGDETAILAPI+'?task_id=' + task_id + '&q='+this.timestamp)
            .subscribe(lists => {
                console.log(lists);
                resolve(lists);
                }, err => {
               console.log("vbn"+ JSON.stringify(err));
            });
        });
    }
    deleteById(id){
        return new Promise(resolve => {
        this.http.post(this.getAPI.DELETEBYIDAPI+'?id=' + id + '&q='+this.timestamp,{})
            .subscribe(lists => {
                console.log(lists);
                resolve(lists);
                }, err => {
               console.log("vbn"+ JSON.stringify(err));
            });
        });
    }

        getByCsv(link,type='') {
              return new Promise(resolve => {
              this.http.get(this.getAPI.GETBYCSVAPI+'?q='+this.timestamp + '&file=' + link + '&type=' + type)
                .subscribe(data => {
                    console.log(data);
                    resolve(data);
                    }, err => {
                   console.log("vbn"+ JSON.stringify(err));
                   alert(JSON.stringify(err));
                });
            });
        }
        getByFTP(file, server, username, password) {
            return new Promise(resolve => {
            this.http.get(this.getAPI.GETBYFTPAPI+'?q=' + this.timestamp + '&file=' + file + '&server=' + server  + '&username=' + username + '&password=' + password)
                .subscribe(data => {
                    console.log(data);
                    resolve(data);
                    }, err => {
                   console.log("vbn"+ JSON.stringify(err));
                   alert(JSON.stringify(err));
                });
            });
        }
        getByAPI(url, headername, headervalue) {
          let url1 = this.getAPI.GETBYAPI+'?q='+this.timestamp + '&link=' + url + '&headername=' + headername  + '&headervalue=' + encodeURIComponent(headervalue);
          return new Promise(resolve => {
          this.http.get(url1)
              .subscribe(data => {
                  console.log(data);
                  resolve(data);
                  //alert(JSON.stringify(data));
                  }, err => {
                 console.log("vbn"+ JSON.stringify(err));
                 alert(JSON.stringify(err));
              });
          });
      }
      getColNames(table) {
            return new Promise(resolve => {
            this.http.get(this.getAPI.GETCOLNAMESAPI+'?q='+this.timestamp + '&table=' + table)
                .subscribe(data => {
                    console.log(data);
                    resolve(data);
                    }, err => {
                   console.log("vbn"+ JSON.stringify(err));
                   alert(JSON.stringify(err));
                });
            });
        }
        saveTable(table, col, row) {
            return new Promise(resolve => {
            this.http.post(this.getAPI.SAVETABLEAPI+'?q='+this.timestamp + '&table=' + table + '&col=' + col,{ row })
                .subscribe(subscriptionValue => {
                    console.log(subscriptionValue);
                    resolve(subscriptionValue);
                    //alert(JSON.stringify(subscriptionValue));
                    }, err => {
                   console.log("vbn"+ JSON.stringify(err));
                   alert(JSON.stringify(err));
                });
            });
        }
        saveImport(table, col, row, importData) {
            return new Promise(resolve => {
            this.http.post(this.getAPI.GETLOYALTYAPI+'?action=saveImport&oi='+this.oi+'&q='+this.timestamp + '&table=' + table + '&col=' + col,{ row,importData })
                .subscribe(subscriptionValue => {
                    console.log(subscriptionValue);
                    resolve(subscriptionValue);
                    //alert(JSON.stringify(subscriptionValue));
                    }, err => {
                   console.log("vbn"+ JSON.stringify(err));
                   alert(JSON.stringify(err));
                });
            });
        }
        saveSchedule(user_id,srcCols,destCols,table,type,parameters,minute,hour,day,month,weekday,title,task) {
            return new Promise(resolve => {
            this.http.post(this.getAPI.SAVESCHEDULEAPI+'?user_id=' + user_id + '&q='+this.timestamp + '&table=' + table + '&task=' + task + '&title=' + title + '&srcCols=' + srcCols + '&destCols=' + destCols + '&type=' + type + '&minute=' + minute + '&hour=' + hour + '&day=' + day + '&month=' + month + '&weekday=' + weekday,{ parameters })
                .subscribe(subscriptionValue => {
                    console.log(subscriptionValue);
                    resolve(subscriptionValue);
                    //alert(JSON.stringify(subscriptionValue));
                    }, err => {
                   console.log("vbn"+ JSON.stringify(err));
                   alert(JSON.stringify(err));
                });
            });
        }

        saveImportLog(task_id,start_dt,end_dt,status) {
            return new Promise(resolve => {
            this.http.post(this.getAPI.SAVEIMPORTLOGAPI+'?q='+this.timestamp + '&task_id=' + task_id + '&start_dt=' + start_dt + '&end_dt=' + end_dt + '&status=' + status,{})
                .subscribe(subscriptionValue => {
                    console.log(subscriptionValue);
                    resolve(subscriptionValue);
                    //alert(JSON.stringify(subscriptionValue));
                    }, err => {
                   console.log("vbn"+ JSON.stringify(err));
                   alert(JSON.stringify(err));
                });
            });
        }
}
