import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { map } from 'rxjs/operators';
import { environment } from '../../../environments/environment';

@Injectable()
export class loyaltypointService {
    getAPI: any;
    constructor(private http: HttpClient) {
     this.getAPI = environment.api;  }
    public oi = localStorage.getItem('oi');
    public timestamp = new Date().getTime();

    getloyaltypoints(listId) {
        return new Promise(resolve => {
        this.http.get(this.getAPI.GETLOYALTYPOINTSAPI+'?q='+this.timestamp+'&oi='+this.oi+'&action=get&listId='+listId)
            .subscribe(name => {
                console.log(name);
                resolve(name);
                }, err => {
               console.log("vbn"+err);
            });
        });
    }
    getUserLoyaltyPoints(userId) {
        return new Promise(resolve => {
        this.http.get(this.getAPI.GETLOYALTYPOINTSAPI+'?q='+this.timestamp+'&oi='+this.oi+'&action=userid&listId='+userId)
            .subscribe(name => {
                console.log(name);
                resolve(name);
                }, err => {
               console.log("vbn"+err);
            });
        });
    }
    getallloyaltypoints(){
        return new Promise(resolve => {
        this.http.get(this.getAPI.GETLOYALTYPOINTSAPI+'?q='+this.timestamp+'&oi='+this.oi+'&action=getall')
            .subscribe(name => {
                console.log(name);
                resolve(name);
                }, err => {
               console.log("vbn"+err);
            });
        });
    }
    addloyaltypoint(loyaltypoint) {
        return new Promise(resolve => {
        this.http.post(this.getAPI.GETLOYALTYPOINTSAPI+'?q='+this.timestamp+'&oi='+this.oi+'&action=add', { loyaltypoint })
            .subscribe(name => {
                console.log(name);
                resolve(name);
                }, err => {
               console.log("vbn"+err);
            });
        });
    }
}
