import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { map } from 'rxjs/operators';
import { environment } from '../../../environments/environment';

@Injectable()
export class OrganizationService {
    getAPI: any;
    constructor(private http: HttpClient) {
     this.getAPI = environment.api;  }

    public timestamp = new Date().getTime();

    saveOrganization(organizationDetail,moduleSettings,userData) {
        return new Promise(resolve => {
        this.http.post(this.getAPI.SAVEORGANIZATIONAPI+'?oi='+ localStorage.getItem('oi')+'&q='+new Date(), { organizationDetail,moduleSettings,userData })
            .subscribe(subscriptionValue => {
                console.log(subscriptionValue);
                resolve(subscriptionValue);
                }, err => {
               console.log("vbn"+ JSON.stringify(err));
            });
        });
    }

    ModuleSettings(moduleSettings, action, orgId){
      return new Promise(resolve => {
      this.http.post(this.getAPI.MODULESETTINGSAPI+'?action='+action+'&oi='+encodeURIComponent(window.btoa(orgId+'_u$0r'))+'&q='+new Date().getTime(), { moduleSettings })
          .subscribe(settingValue => {
              console.log(settingValue);
              resolve(settingValue);
              }, err => {
             console.log("vbn"+ JSON.stringify(err));
          });
      });
    }

    updateOrganization(organizationDetail){
        return new Promise(resolve => {
        this.http.post(this.getAPI.GETORGANIZATIONAPI+'?action=updateorganization&oi='+ localStorage.getItem('oi')+'&q='+new Date().getTime(), { organizationDetail })
            .subscribe(subscriptionValue => {
                console.log(subscriptionValue);
                resolve(subscriptionValue);
                }, err => {
               console.log("vbn"+ JSON.stringify(err));
            });
        });
    }

    getOrganization(timestamp) {
        return new Promise(resolve => {
        this.http.get(this.getAPI.GETORGANIZATIONAPI+'?action=getorganization&oi='+ localStorage.getItem('oi')+'&q='+timestamp)
            .subscribe(data => {
                console.log(data);
                resolve(data);
                }, err => {
               console.log("vbn"+ JSON.stringify(err));
            });
        });
    }

    getOrganizationById(id) {
        return new Promise(resolve => {
        this.http.post(this.getAPI.GETORGANIZATIONAPI+'?action=getorganizationbyid&oi='+ localStorage.getItem('oi')+'&q='+new Date(), { id })
            .subscribe(data => {
                console.log(data);
                resolve(data);
                }, err => {
               console.log("vbn"+ JSON.stringify(err));
            });
        });
    }

    getAdminsByOrgId(id) {
        return new Promise(resolve => {
        this.http.post(this.getAPI.GETORGANIZATIONAPI+'?action=getadminsbyorgid&oi='+ localStorage.getItem('oi')+'&q='+new Date(), { id })
            .subscribe(data => {
                console.log(data);
                resolve(data);
                }, err => {
               console.log("vbn"+ JSON.stringify(err));
            });
        });
    }

    getCharts() {
        return new Promise(resolve => {
        this.http.get(this.getAPI.GETCHARTAPI+'?action=getChartsForAdmin&oi='+ localStorage.getItem('oi')+'&q='+new Date())
            .subscribe(data => {
                console.log(data);
                resolve(data);
                }, err => {
               console.log("vbn"+ JSON.stringify(err));
            });
        });
    }

    getTiles() {
        return new Promise(resolve => {
        this.http.get(this.getAPI.GETCHARTAPI+'?action=getTilesForAdmin&oi='+ localStorage.getItem('oi')+'&q='+new Date())
            .subscribe(data => {
                console.log(data);
                resolve(data);
                }, err => {
               console.log("vbn"+ JSON.stringify(err));
            });
        });
    }

    getListCount() {
        return new Promise(resolve => {
        this.http.get(this.getAPI.GETCHARTAPI+'?action=getReportsForAdmin&oi='+ localStorage.getItem('oi')+'&q='+new Date())
            .subscribe(data => {
                console.log(data);
                resolve(data);
                }, err => {
               console.log("vbn"+ JSON.stringify(err));
            });
        });
    }

    updateStatus(status, id){
        return new Promise(resolve => {
        this.http.post(this.getAPI.GETCHARTAPI+'?action=update&oi='+ localStorage.getItem('oi')+'|&q='+new Date(), {status,id})
            .subscribe(data => {
                console.log(data);
                resolve(data);
                }, err => {
               console.log("vbn"+ JSON.stringify(err));
            });
        });
    }
}
