import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { map } from 'rxjs/operators';
import { environment } from '../../../environments/environment';

@Injectable()
export class EventMappingService {
  getAPI: any;
    constructor(private http: HttpClient) {
    this.getAPI = environment.api;}

    getEventMapping(){
      return new Promise(resolve => {
      this.http.get(this.getAPI.EVENTMAPPINGAPI+'?action=get&oi='+localStorage.getItem('oi')+'&q='+new Date().getTime())
          .subscribe(emailtem => {
              console.log(emailtem);
              resolve(emailtem);
              }, err => {
             console.log("vbn"+ JSON.stringify(err));
          });
      });
    }

    saveMapping(eventMapping){
      return new Promise(resolve => {
      this.http.post(this.getAPI.EVENTMAPPINGAPI+'?action=add&oi='+localStorage.getItem('oi')+'&q='+new Date().getTime(), { eventMapping })
          .subscribe(emailtem => {
              console.log(emailtem);
              resolve(emailtem);
              }, err => {
             console.log("vbn"+ JSON.stringify(err));
          });
      });
    }

    deleteMapping(id){
      return new Promise(resolve => {
      this.http.post(this.getAPI.EVENTMAPPINGAPI+'?action=delete&oi='+localStorage.getItem('oi')+'&q='+new Date().getTime(), { id })
          .subscribe(emailtem => {
              console.log(emailtem);
              resolve(emailtem);
              }, err => {
             console.log("vbn"+ JSON.stringify(err));
          });
      });
    }

    editMapping(id){
      return new Promise(resolve => {
      this.http.post(this.getAPI.EVENTMAPPINGAPI+'?action=edit&oi='+localStorage.getItem('oi')+'&q='+new Date().getTime(), { id })
          .subscribe(emailtem => {
              console.log(emailtem);
              resolve(emailtem);
              }, err => {
             console.log("vbn"+ JSON.stringify(err));
          });
      });
    }

    updateMapping(eventMapping){
      return new Promise(resolve => {
      this.http.post(this.getAPI.EVENTMAPPINGAPI+'?action=update&oi='+localStorage.getItem('oi')+'&q='+new Date().getTime(), { eventMapping })
          .subscribe(emailtem => {
              console.log(emailtem);
              resolve(emailtem);
              }, err => {
             console.log("vbn"+ JSON.stringify(err));
          });
      });
    }
}
