import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { map } from 'rxjs/operators';
import { environment } from '../../../environments/environment';

@Injectable()
export class redeemloyaltyService {
    getAPI: any;
    constructor(private http: HttpClient) {
    this.getAPI = environment.api }
    public oi = localStorage.getItem('oi');

    getdata() {
        return new Promise(resolve => {
        this.http.get(this.getAPI.GETDATAAPI+'?action=get&oi='+ localStorage.getItem('oi') +'&q='+new Date().getTime())
            .subscribe(points => {
                resolve(points);
                }, err => {
               console.log("vbn"+JSON.stringify(err));
            });
        });
    }


    getdataByuser(loyaltypoint) {
            return new Promise(resolve => {
            this.http.post(this.getAPI.GETDATAAPI+'?q='+new Date().getTime()+'&action=getuser&oi='+this.oi, { loyaltypoint })
                .subscribe(name => {
                    resolve(name);
                    }, err => {
                   console.log("vbn"+err);
                });
            });
        }


    updateFilters(loyaltypoint) {
        return new Promise(resolve => {
        this.http.post(this.getAPI.GETDATAAPI+'?q='+new Date().getTime()+'&action=filters&oi='+this.oi, { loyaltypoint })
            .subscribe(name => {
                resolve(name);
                }, err => {
               console.log("vbn"+err);
            });
        });
    }
    updateFiltersbyuser(loyaltypoint,id) {
        return new Promise(resolve => {
        this.http.post(this.getAPI.GETDATAAPI+'?q='+new Date().getTime()+'&action=filterbyuser&oi='+this.oi, { loyaltypoint,id })
            .subscribe(name => {
                resolve(name);
                }, err => {
               console.log("vbn"+err);
            });
        });
    }
}
