    addCampaign(mydata){
      return new Promise(resolve => {
        this.http.post(this.getAPI.ADDCAMPAIGNAPI+'?action=add&oi='+this.oi+'&q='+Date.now(), { data: mydata })
        .subscribe(data => {
          console.log(data);
          resolve(data);
        }, err => {
          console.log("vbn"+err);
        });
      });
    }

