import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { map } from 'rxjs/operators';
import { environment } from '../../../environments/environment';

@Injectable()
export class SubscriptionService {
    getAPI: any;
    constructor(private http: HttpClient) {
    this.getAPI = environment.api }

    public timestamp = new Date().getTime();

    saveSubscription(subscriptionDetail) {
        return new Promise(resolve => {
        this.http.post(this.getAPI.SAVESUBSCRIPTIONAPI+'?action=add&oi='+ localStorage.getItem('oi') +'&q='+this.timestamp, { subscriptionDetail })
            .subscribe(subscriptionValue => {
                console.log(subscriptionValue);
                resolve(subscriptionValue);
                }, err => {
               console.log("vbn"+ JSON.stringify(err));
            });
        });
    }

    getmemSubscription(timestamp) {
        return new Promise(resolve => {
        this.http.get(this.getAPI.GETSUBSCRIPTIONAPI+'?action=getmemplans&oi='+ localStorage.getItem('oi')+'&q='+timestamp)
            .subscribe(settingValue => {
                console.log(settingValue);
                resolve(settingValue);
                }, err => {
               console.log("vbn"+ JSON.stringify(err));
            });
        });
    }

    getSubscription(timestamp) {
        return new Promise(resolve => {
        this.http.get(this.getAPI.GETSUBSCRIPTIONAPI+'?action=getsubscription&oi='+ localStorage.getItem('oi')+'&q='+timestamp)
            .subscribe(settingValue => {
                console.log(settingValue);
                resolve(settingValue);
                }, err => {
               console.log("vbn"+ JSON.stringify(err));
            });
        });
    }

    getSubscriptionByType(type) {
        return new Promise(resolve => {
        this.http.post(this.getAPI.GETSUBSCRIPTIONBYTYPEAPI+'?q='+this.timestamp+'&action=get&oi='+ localStorage.getItem('oi'),{ type })
            .subscribe(users => {
                console.log(users);
                resolve(users);
                }, err => {
               console.log("vbn"+JSON.stringify(err));
            });
        });
    }

    getPlans(timestamp) {
        return new Promise(resolve => {
        this.http.get(this.getAPI.GETSUBSCRIPTIONAPI+'?action=getplans&q='+timestamp)
            .subscribe(settingValue => {
                console.log(settingValue);
                resolve(settingValue);
                }, err => {
               console.log("vbn"+ JSON.stringify(err));
            });
        });
    }

    getOrgSubscriptionById(userid) {
        return new Promise(resolve => {
        this.http.post(this.getAPI.GETORGANIZATIONAPI+'?q='+this.timestamp+'&action=getorgsubscriptionbyid&oi='+ localStorage.getItem('oi'),{ userid })
            .subscribe(users => {
                console.log(users);
                resolve(users);
                }, err => {
               console.log("vbn"+JSON.stringify(err));
            });
        });
    }

    updateSubscriptionType(type,id){
        return new Promise(resolve => {
        this.http.post(this.getAPI.GETCHARTAPI+'?action=updatesubtype&oi='+ localStorage.getItem('oi')+'&q='+this.timestamp, {type,id})
            .subscribe(data => {
                console.log(data);
                resolve(data);
                }, err => {
               console.log("vbn"+ JSON.stringify(err));
            });
        });
    }

    getSubscribers(){
        return new Promise(resolve => {
        this.http.get(this.getAPI.SAVESUBSCRIPTIONAPI+'?action=getorgsubscribers&q='+this.timestamp)
            .subscribe(data => {
                console.log(data);
                resolve(data);
                }, err => {
               console.log("vbn"+ JSON.stringify(err));
            });
        });
    }
}
