import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { map } from 'rxjs/operators';
import { environment } from '../../../environments/environment';

@Injectable()
export class TemplateMappingService {
    getAPI: any;
    constructor(private http: HttpClient) {
    this.getAPI = environment.api }

    gettemplateMapping(){
      return new Promise(resolve => {
      this.http.get(this.getAPI.SAVEMAPPINGTEMPLATEAPI+'?action=get&oi='+localStorage.getItem('oi')+'&q='+new Date().getTime())
          .subscribe(emailtem => {
              console.log(emailtem);
              resolve(emailtem);
              }, err => {
             console.log("vbn"+ JSON.stringify(err));
          });
      });
    }

    getNewsTemp(){
      return new Promise(resolve => {
      this.http.get(this.getAPI.SAVEMAPPINGTEMPLATEAPI+'?action=getemailtemp&oi='+localStorage.getItem('oi')+'&q='+new Date().getTime())
          .subscribe(emailtem => {
              console.log(emailtem);
              resolve(emailtem);
              }, err => {
             console.log("vbn"+ JSON.stringify(err));
          });
      });
    }

    saveMapping(templateMapping){
      return new Promise(resolve => {
      this.http.post(this.getAPI.SAVEMAPPINGTEMPLATEAPI+'?action=add&oi='+localStorage.getItem('oi')+'&q='+new Date().getTime(), { templateMapping })
          .subscribe(emailtem => {
              console.log(emailtem);
              resolve(emailtem);
              }, err => {
             console.log("vbn"+ JSON.stringify(err));
          });
      });
    }

    getTemplateHTML(template){
      return new Promise(resolve => {
      this.http.post(this.getAPI.GETEMAILTEMPAPI+'?action=getHTML&oi='+localStorage.getItem('oi')+'&q='+new Date().getTime(), { template })
          .subscribe(emailtem => {
              console.log(emailtem);
              resolve(emailtem);
              }, err => {
             console.log("vbn"+ JSON.stringify(err));
          });
      });
    }

    deleteMapping(id){
      return new Promise(resolve => {
      this.http.post(this.getAPI.SAVEMAPPINGTEMPLATEAPI+'?action=delete&oi='+localStorage.getItem('oi')+'&q='+new Date().getTime(), { id })
          .subscribe(emailtem => {
              console.log(emailtem);
              resolve(emailtem);
              }, err => {
             console.log("vbn"+ JSON.stringify(err));
          });
      });
    }

    editMapping(id){
      return new Promise(resolve => {
      this.http.post(this.getAPI.SAVEMAPPINGTEMPLATEAPI+'?action=edit&oi='+localStorage.getItem('oi')+'&q='+new Date().getTime(), { id })
          .subscribe(emailtem => {
              console.log(emailtem);
              resolve(emailtem);
              }, err => {
             console.log("vbn"+ JSON.stringify(err));
          });
      });
    }

    updateMapping(templateMapping){
      return new Promise(resolve => {
      this.http.post(this.getAPI.SAVEMAPPINGTEMPLATEAPI+'?action=update&oi='+localStorage.getItem('oi')+'&q='+new Date().getTime(), { templateMapping })
          .subscribe(emailtem => {
              console.log(emailtem);
              resolve(emailtem);
              }, err => {
             console.log("vbn"+ JSON.stringify(err));
          });
      });
    }
}
