    getStats(){
      return new Promise(resolve => {
      this.http.get(this.getAPI.GETWEBSTATSAPI+'?action=get&oi='+localStorage.getItem('oi')+'&q='+new Date().getTime())
          .subscribe(stats => {
              console.log(stats);
              resolve(stats);
              }, err => {
             console.log("vbn"+ JSON.stringify(err));
          });
      });
    }

