import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { map } from 'rxjs/operators';
import { environment } from '../../../environments/environment';

@Injectable()
export class LuckyDrawService {
    getAPI: any;
    constructor(private http: HttpClient) {
    this.getAPI = environment.api }

    public timestamp = new Date().getTime();

    getDetails() {
      return new Promise(resolve => {
      this.http.get(this.getAPI.GETLUCKYDRAWAPI+'?action=get&oi='+ localStorage.getItem('oi') +'&q='+new Date().getTime())
          .subscribe(users => {
              console.log(users);
              resolve(users);
              }, err => {
             console.log("vbn"+JSON.stringify(err));
          });
      });
    }
    generateLuckyDraw() {
      return new Promise(resolve => {
      this.http.get(this.getAPI.GETLUCKYDRAWAPI+'?action=generate&q='+new Date().getTime()+'&oi='+ localStorage.getItem('oi'))
          .subscribe(users => {
              console.log(users);
              resolve(users);
              }, err => {
             console.log("vbn"+JSON.stringify(err));
          });
      });
    }
    insertLuckyDraw(details) {
      return new Promise(resolve => {
      this.http.post(this.getAPI.GETLUCKYDRAWAPI+'?action=insert&q='+new Date().getTime()+'&oi='+ localStorage.getItem('oi'), {details:details})
          .subscribe(users => {
              console.log(users);
              resolve(users);
              }, err => {
             console.log("vbn"+JSON.stringify(err));
          });
      });
    }
}
