import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { map } from 'rxjs/operators';
import { environment } from '../../../environments/environment';

@Injectable()
export class TransactionsService {
    getAPI: any;
    constructor(private http: HttpClient) { this.getAPI = environment.api }
    public timestamp = new Date().getTime();
    public oi = localStorage.getItem('oi');
    getTransaction() {
        return new Promise(resolve => {
        this.http.get(this.getAPI.ADDTRANSACTIONAPI+'?action=getTransaction&oi='+ localStorage.getItem('oi') +'&q='+this.timestamp)
            .subscribe(users => {
                console.log(users);
                resolve(users);
                }, err => {
               console.log("vbn"+JSON.stringify(err));
            });
        });
    }
}
