import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { map } from 'rxjs/operators';
import { environment } from '../../../environments/environment';

@Injectable()
export class crewsService {
  getAPI: any;

    constructor(private http: HttpClient) {
      this.getAPI = environment.api;  }

    public timestamp = new Date().getTime();
    public oi = localStorage.getItem('oi');

      sendmail(email) {
          return new Promise(resolve => {
            this.http.post(this.getAPI.SENDMAILAPI+'?q='+new Date().getTime()+'&action=sendmail&oi='+this.oi, { email })
              .subscribe(name => {
                  resolve(name);
                  }, err => {
                 console.log("vbn"+JSON.stringify(err));
              });
          });
      }
      getDataForCrew() {
          return new Promise(resolve => {
            this.http.post(this.getAPI.SENDMAILAPI+'?q='+new Date().getTime()+'&action=getdata&oi='+this.oi, {  })
              .subscribe(name => {
                  resolve(name);
                  }, err => {
                 console.log("vbn"+JSON.stringify(err));
              });
          });
      }
  }
