import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { map } from 'rxjs/operators';
import { environment } from '../../../environments/environment';

@Injectable()
export class RenewalService {
    getAPI: any;
    constructor(private http: HttpClient) {
    this.getAPI = environment.api }

    public timestamp = new Date().getTime();

    getRenewal() {
        return new Promise(resolve => {
        this.http.get(this.getAPI.GETRENEWALAPI+'?q='+this.timestamp+'&action=get&oi='+ localStorage.getItem('oi'))
            .subscribe(renewal => {
                console.log(renewal);
                resolve(renewal);
                }, err => {
               console.log("vbn"+ JSON.stringify(err));
            });
        });
    }

    sendRenewalEmail(renewalId, userEmail, userName, plans, expire_date){
        let renewalDetails = {
            'renewalId' : renewalId,
            'userEmail' : userEmail,
            'userName' : userName,
            'plans' : plans,
            'expire_date' : expire_date
        }
        return new Promise(resolve => {
        this.http.post(this.getAPI.GETRENEWALAPI+'?q='+this.timestamp+'&action=send&oi='+ localStorage.getItem('oi'),{renewalDetails})
            .subscribe(sentRenewal => {
                console.log(sentRenewal);
                resolve(sentRenewal);
                }, err => {
               console.log("vbn"+ JSON.stringify(err));
            });
        });
    }
}
